<template>
  <div>
    <mu-appbar>
      <router-link to="/" @click.native="back()">Rare</router-link>
      <mu-icon-button icon="menu" slot="left" @click="toggle()"/>
      <mu-avatar v-show="login" slot="right" :src="avatar"/>
      <mu-icon-menu v-show="login" @change="handleChange" icon="more_vert" slot="right">
        <mu-menu-item value="1" title="退出登录" @click="logout"/>
      </mu-icon-menu>
    </mu-appbar>
    <mu-drawer left :open="open" @close="toggle()" :docked=false>
      <mu-appbar title="Rare"/>
      <mu-list :value="value2" @change="handleChange2">
        <mu-list-item :open="false" :value="1" title="文章" inset toggleNested>
          <mu-icon slot="left" value="grade"/>
          <mu-list-item v-for="post in (posts ? posts: articles)" :key= "post.id" :value="post.id"  :title="post.title" slot="nested">
          </mu-list-item>
        </mu-list-item>
      </mu-list>
    </mu-drawer>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      open: false,
      value: '1',
      value2: '1',
      avatar: window.sessionStorage.getItem('avatar'),
      login: window.sessionStorage.getItem('key'),
      body: '',
      articles: ''
    }
  },
  props: ['posts'],
  methods: {
    toggle () {
      this.open = !this.open
    },
    logout () {
      let _this = this
      _this.login = false
      window.sessionStorage.removeItem('key')
      window.sessionStorage.removeItem('admin')
      window.sessionStorage.removeItem('avatar')
      axios({
        method: 'DELETE',
        url: '/api/sessions',
        headers: {
          'Content-Type': 'application/json'
        }
      }).then(function (response) {
        _this.$router.push({'name': 'HelloWorld'})
      }).catch(function (error) {
        console.log(error)
      })
    },
    handleChange (val) {
      if (this.value === val) {
        this.logout()
      }
    },
    handleChange2 (val) {
      /* eslint-disable */
      if (val != this.value2) {
        this.$router.push({name: 'Detail', params: {id: val}})
      }
    },
    fetchData () {
      var _this = this
      if (_this.$route.name === 'Detail') {
        axios({
          method: 'GET',
          url: '/api/posts',
          headers: {
            'Content-Type': 'application/json'
          }
        }).then(function (response) {
          _this.articles = response.data.posts
        }).catch(function (error) {
          console.log(error)
        })
      }
    }
  },
  mounted () {
    this.fetchData()
  }
}
</script>


