<template>
  <div class="hello">
      <appbar :posts="posts"></appbar>
      <div class="posts-wrapper">
        <mu-row>
          <mu-col width="0" tablet="15" desktop="25"></mu-col>
          <mu-col width="100" tablet="70" desktop="50">
            <mu-card v-for="post in posts" v-bind:key="post.id">
              <mu-card-header v-bind:title="post.author" v-bind:subTitle="post.created_time | moment">
                <mu-avatar :src="post.author_avatar" slot="avatar"/>
              </mu-card-header>
              <mu-card-media>
                <img v-bind:src="post.img.url">
              </mu-card-media>
              <mu-card-title v-bind:title="post.title" :subTitle="post.category"></mu-card-title>
              <mu-card-text>
                {{ post.desc }} <router-link :to="{ name: 'Detail', params: { id: post.id }}">继续阅读</router-link>
              </mu-card-text>
            </mu-card>
          </mu-col>
          <mu-col width="0" tablet="15" desktop="25"></mu-col>
        </mu-row>
      </div>
  </div>
</template>

<script>
import axios from 'axios'
import appbar from '@/components/Appbar'

export default {
  name: 'HelloWorld',
  data () {
    return {
      value: '1',
      avatar: window.sessionStorage.getItem('avatar'),
      login: window.sessionStorage.getItem('key'),
      posts: []
    }
  },
  methods: {
    fetchData () {
      var _this = this
      axios({
        method: 'GET',
        url: '/api/posts',
        headers: {
          'Content-Type': 'application/json'
        }
      }).then(function (response) {
        _this.posts = response.data.posts
      }).catch(function (error) {
        console.log(error)
      })
    },
    logout () {
      let _this = this
      _this.login = false
      window.sessionStorage.removeItem('key')
      window.sessionStorage.removeItem('admin')
      window.sessionStorage.removeItem('avatar')
      axios({
        method: 'DELETE',
        url: '/api/sessions',
        headers: {
          'Content-Type': 'application/json'
        }
      }).then(function (response) {
        _this.$router.push({'name': 'HelloWorld'})
      }).catch(function (error) {
        console.log(error)
      })
    },
    handleChange (val) {
      if (this.value === val) {
        this.logout()
      }
    }
  },
  mounted () {
    this.fetchData()
  },
  components: {
    appbar
  }
}
</script>


